import React from 'react';
import { Trophy, Skull, Clock, Tv } from 'lucide-react';
import PlayerList from './PlayerList';

const statusStyles = {
  'Not Started': {
    color: 'text-gray-400',
    bgColor: 'bg-gray-800',
    icon: <Clock size={16} />,
  },
  'Winner Bracket': {
    color: 'text-green-400',
    bgColor: 'bg-green-900/30',
    icon: <Trophy size={16} />,
  },
  'Loser Bracket': {
    color: 'text-yellow-400',
    bgColor: 'bg-yellow-900/30',
    icon: <Trophy size={16} />,
  },
  'Eliminated': {
    color: 'text-red-400',
    bgColor: 'bg-red-900/30',
    icon: <Skull size={16} />,
  },
};

const TeamCard = ({ team }) => {
  const status = statusStyles[team.status];

  return (
    <div className="relative overflow-hidden rounded-xl border border-gray-700 bg-gray-800 p-2 md:p-4">
      {/* Fondo con imagen o fallback */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute inset-0 flex items-center justify-center opacity-45">
          {team.image ? (
            <picture>
              {/* Para móviles, se usa una versión reducida de calidad */}
              <source media="(max-width: 640px)" srcSet={`${team.image}?quality=50`} />
              <img src={team.image} alt={team.name} className="w-full h-full object-cover" />
            </picture>
          ) : (
            <span className="text-3xl md:text-[150px] font-bold text-white select-none">
              {team.name.substring(0, 2).toUpperCase()}
            </span>
          )}
        </div>
      </div>

      {/* Estado del equipo */}
      <div className={`absolute top-0 right-0 ${status.bgColor} px-2 py-1 rounded-bl-xl flex items-center gap-1 z-10`}>
        {status.icon}
        <span className={`${status.color} text-xs md:text-sm font-medium`}>{team.status}</span>
      </div>

      {/* Botón de stream (si existe) */}
      {team.streamUrl && (
        <a
          href={team.streamUrl}
          target="_blank"
          rel="noopener noreferrer"
          className="absolute top-2 left-2 px-2 py-1 rounded-lg z-10 flex items-center gap-1 bg-gray-800/80 backdrop-blur-sm transition-colors duration-300 group"
        >
          <Tv size={12} className="text-gray-400 group-hover:text-red-500 transition-colors duration-300" />
          <span className="text-xs font-medium text-gray-400 group-hover:text-red-500 transition-colors duration-300">LIVE</span>
        </a>
      )}

      {/* Información del equipo */}
      <div className="pt-4 pb-2 relative z-10">
        <h3 className="text-lg md:text-xl font-bold text-white mb-1">{team.name}</h3>
        <div className="flex items-center gap-1 text-gray-400">
          <Trophy size={14} />
          <span className="text-xs md:text-sm">Seed #{team.seed}</span>
        </div>
      </div>

      {/* Lista de jugadores */}
      <div className="relative z-10">
        <h4 className="text-xs md:text-sm font-medium text-gray-400 mb-1">Roster</h4>
        <PlayerList players={team.players} />
      </div>
    </div>
  );
};

export default TeamCard;
